import { StaticImage } from 'gatsby-plugin-image';
import React, {
  useCallback,
  useContext,
  useEffect,
  useLayoutEffect,
  useMemo,
  useRef,
  useState,
} from 'react';

import { HeaderContext } from '../Header/context';
import * as s from './style.module.scss';

export const alt = "Da-Woon and Minjoo's Wedding";

const D_FACE_POSITION_MULTIPLIER_X = 0.485;
const D_FACE_POSITION_MULTIPLIER_Y = 0.593;

const M_FACE_POSITION_MULTIPLIER_X = 0.62;
const M_FACE_POSITION_MULTIPLIER_Y = 0.62;

function Avatars() {
  const { distanceLeft, distanceRight, finalProgress, finalReveal } =
    useContext(HeaderContext);
  const ref = useRef<HTMLDivElement | null>(null);
  const observer = useRef<ResizeObserver | null>(null);
  const canvasR = useRef<HTMLCanvasElement | null>(null);
  const canvasD = useRef<HTMLCanvasElement | null>(null);
  const canvasM = useRef<HTMLCanvasElement | null>(null);
  const contextR = useRef<CanvasRenderingContext2D | null>(null);
  const contextD = useRef<CanvasRenderingContext2D | null>(null);
  const contextM = useRef<CanvasRenderingContext2D | null>(null);
  const [canvasWidth, setCanvasWidth] = useState<number>(0);
  const [canvasHeight, setCanvasHeight] = useState<number>(0);
  const image = useRef<HTMLImageElement | null>(null);
  const [drawWidth, setDrawWidth] = useState<number>(0);
  const [drawHeight, setDrawHeight] = useState<number>(0);
  const [offsetX, setOffsetX] = useState<number>(0);
  const [offsetY, setOffsetY] = useState<number>(0);
  const [lineStartX, setLineStartX] = useState<number>(0);
  const [lineStartY, setLineStartY] = useState<number>(0);
  const [lineEndX, setLineEndX] = useState<number>(0);
  const [lineEndY, setLineEndY] = useState<number>(0);
  const [avatarSize, setAvatarSize] = useState<number>(0);
  const [forceUpdate, setForceUpdate] = useState({});

  const faceCenterXD = useMemo(
    () => drawWidth * D_FACE_POSITION_MULTIPLIER_X,
    [drawWidth],
  );
  const faceCenterYD = useMemo(
    () => drawHeight * D_FACE_POSITION_MULTIPLIER_Y,
    [drawHeight],
  );
  const faceCenterXM = useMemo(
    () => drawWidth * M_FACE_POSITION_MULTIPLIER_X,
    [drawWidth],
  );
  const faceCenterYM = useMemo(
    () => drawHeight * M_FACE_POSITION_MULTIPLIER_Y,
    [drawHeight],
  );

  // Final positions just before entering the final stage.
  // Equivalent to distance = -1.
  const offsetXD = useMemo(
    () => canvasWidth - faceCenterXD + avatarSize * 1.1,
    [avatarSize, canvasWidth, faceCenterXD],
  );

  const offsetYD = useMemo(
    () => canvasHeight - faceCenterYD + avatarSize * 0.5,
    [avatarSize, canvasHeight, faceCenterYD],
  );

  const circleXD = useMemo(
    () => canvasWidth - avatarSize * 0.5 + avatarSize * 1.1,
    [avatarSize, canvasWidth],
  );

  const offsetXM = useMemo(
    () => canvasWidth - faceCenterXM - avatarSize * 0.1,
    [avatarSize, canvasWidth, faceCenterXM],
  );

  const offsetYM = useMemo(
    () => canvasHeight - faceCenterYM + avatarSize * 0.5,
    [avatarSize, canvasHeight, faceCenterYM],
  );

  const circleXM = useMemo(
    () => canvasWidth - avatarSize * 0.6,
    [avatarSize, canvasWidth],
  );

  const maxRadius = useMemo(
    () =>
      Math.sqrt(canvasWidth ** 2 + canvasHeight ** 2) +
      Math.sqrt((offsetX / 2) ** 2 + (offsetY / 2) ** 2),
    [canvasWidth, canvasHeight, offsetX, offsetY],
  );

  const onCanvasRInit = useCallback((node: HTMLCanvasElement) => {
    if (!node) return;
    canvasR.current = node;
    contextR.current = node.getContext('2d');
  }, []);

  const onCanvasDInit = useCallback((node: HTMLCanvasElement) => {
    if (!node) return;
    canvasD.current = node;
    contextD.current = node.getContext('2d');
  }, []);

  const onCanvasMInit = useCallback((node: HTMLCanvasElement) => {
    if (!node) return;
    canvasM.current = node;
    contextM.current = node.getContext('2d');
  }, []);

  const callback = useCallback(() => {
    if (!ref.current || !canvasD.current || !canvasM.current) return;

    const imgs = ref.current.getElementsByTagName('img');
    const img = Array.from(imgs).find((_img) => _img.alt === alt);

    if (img) {
      image.current = img;
      setForceUpdate({});
    }
  }, []);

  const configureSize = useCallback(() => {
    if (!image.current) return;
    if (!canvasWidth || !canvasHeight) return; // Also when they are 0.

    const { width, height } = image.current;

    const imageRatio = width / height;
    const canvasRatio = canvasWidth / canvasHeight;

    /* eslint-disable @typescript-eslint/naming-convention, no-underscore-dangle */
    let _drawWidth;
    let _drawHeight;
    let _offsetX;
    let _offsetY;
    /* eslint-enable @typescript-eslint/naming-convention, no-underscore-dangle */

    if (imageRatio > canvasRatio) {
      _drawWidth = canvasHeight * 2 * imageRatio;
      _drawHeight = canvasHeight * 2;
      _offsetX = (canvasWidth * 2 - _drawWidth) / 2;
      _offsetY = 0;
    } else {
      _drawWidth = canvasWidth * 2;
      _drawHeight = canvasWidth * 2 * (1 / imageRatio);
      _offsetX = 0;
      _offsetY = (canvasHeight * 2 - _drawHeight) / 2;
    }

    // eslint-disable-next-line @typescript-eslint/naming-convention, no-underscore-dangle
    const _avatarSize = _drawWidth * 0.1;

    setDrawWidth(_drawWidth);
    setDrawHeight(_drawHeight);
    setOffsetX(_offsetX);
    setOffsetY(_offsetY);
    setAvatarSize(_avatarSize);
  }, [canvasHeight, canvasWidth]);

  const drawR = useCallback(() => {
    if (!contextR.current) return;

    const ctxR = contextR.current;

    ctxR.clearRect(0, 0, canvasWidth * 2, canvasHeight * 2);
    ctxR.beginPath();
    ctxR.setLineDash([10, 10]);
    ctxR.lineWidth = 3;
    ctxR.strokeStyle = '#faebd7';
    ctxR.moveTo(lineStartX, lineStartY + avatarSize * 0.6);
    ctxR.lineTo(lineEndX, lineEndY + avatarSize * 0.6);
    ctxR.stroke();
  }, [
    avatarSize,
    canvasWidth,
    canvasHeight,
    lineStartX,
    lineStartY,
    lineEndX,
    lineEndY,
  ]);

  const drawD = useCallback(() => {
    if (!image.current) return;
    if (!contextD.current) return;

    const ctxD = contextD.current;

    const leftEdge = avatarSize * 0.5;
    const rightEdge = canvasWidth - avatarSize * 0.5;

    let delta;

    if (distanceLeft >= 0) {
      delta = (rightEdge - leftEdge) * (distanceLeft / 10);
    } else {
      delta = avatarSize * 1.1 * distanceLeft;
    }

    let finalOffsetX;
    let finalOffsetY;

    if (finalProgress > 0) {
      finalOffsetX = (offsetX - offsetXD) * finalProgress + offsetXD;
      finalOffsetY = (offsetY - offsetYD) * finalProgress + offsetYD;
    } else {
      finalOffsetX = canvasWidth - faceCenterXD - delta;
      finalOffsetY = canvasHeight - faceCenterYD + avatarSize * 0.5;
    }

    ctxD.clearRect(0, 0, canvasWidth * 2, canvasHeight * 2);
    ctxD.drawImage(
      image.current,
      finalOffsetX,
      finalOffsetY,
      drawWidth,
      drawHeight,
    );

    let circleX;
    let circleY;

    if (finalProgress > 0) {
      circleX =
        (faceCenterXD + offsetX - avatarSize * 0.5 - circleXD) * finalProgress +
        circleXD;
      circleY =
        (faceCenterYD + offsetY - avatarSize * 0.5 - canvasHeight) *
          finalProgress +
        canvasHeight;
    } else {
      circleX = rightEdge - delta;
      circleY = canvasHeight;
    }

    setLineStartX(circleX);
    setLineStartY(circleY);

    ctxD.globalCompositeOperation = 'destination-in';
    ctxD.fillStyle = '#000';
    ctxD.beginPath();
    ctxD.arc(
      circleX,
      circleY,
      finalReveal > 0
        ? (maxRadius - avatarSize * 0.5) * finalReveal + avatarSize * 0.5
        : avatarSize * 0.5,
      0,
      2 * Math.PI,
    );
    ctxD.fill();
    ctxD.globalCompositeOperation = 'source-over';
  }, [
    avatarSize,
    canvasWidth,
    canvasHeight,
    circleXD,
    distanceLeft,
    drawWidth,
    drawHeight,
    faceCenterXD,
    faceCenterYD,
    finalProgress,
    finalReveal,
    maxRadius,
    offsetX,
    offsetY,
    offsetXD,
    offsetYD,
  ]);

  const drawM = useCallback(() => {
    if (!image.current) return;
    if (!contextM.current) return;

    const ctxM = contextM.current;

    const leftEdge = canvasWidth + avatarSize * 0.5;
    const rightEdge = canvasWidth * 2 - avatarSize * 0.5;

    let delta;

    if (distanceRight >= 0) {
      delta = (leftEdge - rightEdge) * (distanceRight / 10);
    } else {
      delta = -avatarSize * 1.1 * distanceRight;
    }

    let finalOffsetX;
    let finalOffsetY;

    if (finalProgress > 0) {
      finalOffsetX = (offsetX - offsetXM) * finalProgress + offsetXM;
      finalOffsetY = (offsetY - offsetYM) * finalProgress + offsetYM;
    } else {
      finalOffsetX = canvasWidth - faceCenterXM + avatarSize - delta;
      finalOffsetY = canvasHeight - faceCenterYM + avatarSize * 0.5;
    }

    ctxM.clearRect(0, 0, canvasWidth * 2, canvasHeight * 2);
    ctxM.drawImage(
      image.current,
      finalOffsetX,
      finalOffsetY,
      drawWidth,
      drawHeight,
    );

    let circleX;
    let circleY;

    if (finalProgress > 0) {
      circleX =
        (faceCenterXM + offsetX - avatarSize * 0.5 - circleXM) * finalProgress +
        circleXM;
      circleY =
        (faceCenterYM + offsetY - avatarSize * 0.5 - canvasHeight) *
          finalProgress +
        canvasHeight;
    } else {
      circleX = leftEdge - delta;
      circleY = canvasHeight;
    }

    setLineEndX(circleX);
    setLineEndY(circleY);

    ctxM.globalCompositeOperation = 'destination-in';
    ctxM.fillStyle = '#000';
    ctxM.beginPath();
    ctxM.arc(
      circleX,
      circleY,
      finalReveal > 0
        ? (maxRadius - avatarSize * 0.5) * finalReveal + avatarSize * 0.5
        : avatarSize * 0.5,
      0,
      2 * Math.PI,
    );
    ctxM.fill();
    ctxM.globalCompositeOperation = 'source-over';
  }, [
    avatarSize,
    canvasWidth,
    canvasHeight,
    circleXM,
    distanceRight,
    drawWidth,
    drawHeight,
    faceCenterXM,
    faceCenterYM,
    finalProgress,
    finalReveal,
    maxRadius,
    offsetX,
    offsetY,
    offsetXM,
    offsetYM,
  ]);

  useLayoutEffect(() => {
    configureSize();
    drawD();
    drawM();
    drawR();
  }, [
    canvasWidth,
    canvasHeight,
    configureSize,
    drawD,
    drawM,
    drawR,
    forceUpdate,
  ]);

  useEffect(() => {
    observer.current = new ResizeObserver(([entry]) => {
      const { width, height } = entry.contentRect;
      setCanvasWidth(width);
      setCanvasHeight(height);
    });

    observer.current.observe(ref.current!);

    return () => {
      observer.current?.disconnect();
    };
  }, []);

  useEffect(() => {
    if (!ref.current) return;

    const { width, height } = ref.current.getBoundingClientRect();
    setCanvasWidth(width);
    setCanvasHeight(height);
  }, []);

  return useMemo(
    () => (
      <div className={s.wrap} ref={ref}>
        <canvas
          className={s.canvas}
          ref={onCanvasRInit}
          width={canvasWidth * 2}
          height={canvasHeight * 2}
          style={{
            width: `${canvasWidth}px`,
            height: `${canvasHeight}px`,
            zIndex: 0,
          }}
        />
        <canvas
          className={s.canvas}
          ref={onCanvasDInit}
          width={canvasWidth * 2}
          height={canvasHeight * 2}
          style={{
            width: `${canvasWidth}px`,
            height: `${canvasHeight}px`,
            zIndex: 1,
          }}
        />
        <canvas
          className={s.canvas}
          ref={onCanvasMInit}
          width={canvasWidth * 2}
          height={canvasHeight * 2}
          style={{
            width: `${canvasWidth}px`,
            height: `${canvasHeight}px`,
            zIndex: finalProgress > 0 ? 2 : 0,
          }}
        />
        <StaticImage
          src="../../images/main.jpg"
          loading="eager"
          alt={alt}
          style={{ display: 'none' }}
          onLoad={callback}
        />
      </div>
    ),
    [
      callback,
      canvasHeight,
      canvasWidth,
      finalProgress,
      onCanvasRInit,
      onCanvasDInit,
      onCanvasMInit,
    ],
  );
}

export default Avatars;
