import React, {
  Dispatch,
  ReactNode,
  SetStateAction,
  createContext,
  useMemo,
  useState,
} from 'react';

interface HeaderContextInterface {
  altLeft: string;
  altRight: string;
  captionEntered: boolean;
  captionPosition: number;
  captionOpacity: number;
  distanceLeft: number;
  distanceRight: number;
  finalProgress: number;
  finalReveal: number;
  setAltLeft: Dispatch<SetStateAction<string>>;
  setAltRight: Dispatch<SetStateAction<string>>;
  setCaptionEntered: Dispatch<SetStateAction<boolean>>;
  setCaptionOpacity: Dispatch<SetStateAction<number>>;
  setCaptionPosition: Dispatch<SetStateAction<number>>;
  setDistanceLeft: Dispatch<SetStateAction<number>>;
  setDistanceRight: Dispatch<SetStateAction<number>>;
  setFinalProgress: Dispatch<SetStateAction<number>>;
  setFinalReveal: Dispatch<SetStateAction<number>>;
}

export const INITIAL_DISTANCE = 1;

const initialContext: HeaderContextInterface = {
  altLeft: "Da-Woon's Life Path",
  altRight: "Minjoo's Life Path",
  captionEntered: false,
  captionPosition: 0,
  captionOpacity: 1,
  distanceLeft: INITIAL_DISTANCE,
  distanceRight: INITIAL_DISTANCE,
  finalProgress: 0,
  finalReveal: 0,
  setAltLeft: () => {},
  setAltRight: () => {},
  setCaptionEntered: () => {},
  setCaptionPosition: () => {},
  setCaptionOpacity: () => {},
  setDistanceLeft: () => {},
  setDistanceRight: () => {},
  setFinalProgress: () => {},
  setFinalReveal: () => {},
};

export const HeaderContext =
  createContext<HeaderContextInterface>(initialContext);

interface Props {
  children: ReactNode;
}

function HeaderContextProvider({ children }: Props) {
  const [altLeft, setAltLeft] = useState<string>("Da-Woon's Life Path");
  const [altRight, setAltRight] = useState<string>("Minjoo's Life Path");
  const [captionEntered, setCaptionEntered] = useState<boolean>(false);
  const [captionPosition, setCaptionPosition] = useState<number>(0);
  const [captionOpacity, setCaptionOpacity] = useState<number>(1);
  const [distanceLeft, setDistanceLeft] = useState<number>(INITIAL_DISTANCE);
  const [distanceRight, setDistanceRight] = useState<number>(INITIAL_DISTANCE);
  const [finalProgress, setFinalProgress] = useState<number>(0);
  const [finalReveal, setFinalReveal] = useState<number>(0);

  const value = useMemo(
    () => ({
      altLeft,
      altRight,
      captionEntered,
      captionPosition,
      captionOpacity,
      distanceLeft,
      distanceRight,
      finalProgress,
      finalReveal,
      setAltLeft,
      setAltRight,
      setCaptionEntered,
      setCaptionPosition,
      setCaptionOpacity,
      setDistanceLeft,
      setDistanceRight,
      setFinalProgress,
      setFinalReveal,
    }),
    [
      altLeft,
      altRight,
      captionEntered,
      captionPosition,
      captionOpacity,
      distanceLeft,
      distanceRight,
      finalProgress,
      finalReveal,
    ],
  );

  return (
    <HeaderContext.Provider value={value}>{children}</HeaderContext.Provider>
  );
}

export default HeaderContextProvider;
